//react
import React ,{useState, useEffect} from 'react';
import {Link,useHistory} from "react-router-dom";
import serverController from '../../../../server/serverController';

//css
import styled from "styled-components"

//img
import Check from "../../../../img/member/check.png";
import Checked from "../../../../img/member/checked.png";

//added redux actions go
import {useSelector} from 'react-redux';
import {tempRegisterUserdataActions } from '../../../../store/actionCreators';

export default function JoinTab() {
  const history= useHistory();

  console.log('component>member>agency>joinselect  =================');
  const tempregisteruserdata= useSelector(data => data.temp_register_userdata);

  console.log('tdata.temp_register_userdata refer info:',tempregisteruserdata,tempRegisterUserdataActions);

  const [name,setName] = useState("");/**/
  const [phone,setPhone] = useState("");/**/
  const [cernum,setCernum] = useState("");/**/
  const [verify_cernum,setVerify_cernum] = useState("");
  const [isceo,setIsceo] = useState(undefined); //  ()

  const [nextshow,setNextShow] = useState(false);

  const [active,setActive] = useState(false);
  const [active2,setActive2] = useState(false);

  const nameChange = (e) =>{ setName(e.target.value); }
  const phoneChange = (e) =>{ setPhone(e.target.value); }
  const cernumChange = (e) =>{ setCernum(e.target.value); }
 
  // .
  const aligoSmsSend = async(e) => {
    console.log('aligosmsSend :',name,phone,cernum);

    let body_info = {
      receiver : phone,
      msg : 'api test send',
      msg_type : 'SMS',
      title : 'api test.',
      destination : phone+'|'+name,
      //rdate : new Date(),
      //rtime : '',
    };
    
    let res = await serverController.connectFetchController('/api/aligoSms','POST',JSON.stringify(body_info));
    console.log('aligosms send res reusltsss:',res);
    
    document.getElementById('inputcernum').style.display='block';

    //    
    var is_company_ceo_radios=document.getElementsByClassName('is_company_ceo');
    var check_id;
    for(var ss=0; ss<is_company_ceo_radios.length; ss++){
      if(is_company_ceo_radios[ss].checked){
        check_id=is_company_ceo_radios[ss].id;//check1,2
      }
    }
    
    console.log('    :',check_id);
    if(check_id){
      switch(check_id){
        case 'check1':
          //    companyjoininfo 
          document.getElementById('agencyJoinInfo_move').style.display='block';
          document.getElementById('agencyJoinAgree_move').style.display='none';
        break;
  
        case 'check2':
          //     .
          document.getElementById('agencyJoinInfo_move').style.display='block';
          document.getElementById('agencyJoinAgree_move').style.display='none';
        break;
      }
    }else{
      //     . default,   companyjoininfo 
        document.getElementById('agencyJoinInfo_move').style.display='block';
        document.getElementById('agencyJoinAgree_move').style.display='none';
    }
    setNextShow(true);//  true  . .

    //setVerify_cernum(res.message);
  }

  //(       )
  const is_agency_ceo_changes = (e) => {
    console.log('     :',e,e.target);

      //   ( input,      )
      //    
      var is_company_ceo_radios=document.getElementsByClassName('is_agency_ceo');
      var check_id;
      for(var ss=0; ss<is_company_ceo_radios.length; ss++){
        if(is_company_ceo_radios[ss].checked){
          check_id=is_company_ceo_radios[ss].id;//check1,2
        }
      }
      if(check_id){
        if(nextshow){
          switch(check_id){
            case 'check1':
              //    companyjoininfo 
              document.getElementById('agencyJoinInfo_move').style.display='block';
              document.getElementById('agencyJoinAgree_move').style.display='none';
              setIsceo(true);
            break;
      
            case 'check2':
              //    .
              document.getElementById('agencyJoinInfo_move').style.display='block';
              document.getElementById('agencyJoinAgree_move').style.display='none';
              setIsceo(false);
            break;
          }
        }else{
          switch(check_id){
            case 'check1':
              //      
              setIsceo(true);
            break;

            case 'check2':
              setIsceo(false);
            break;
          }
        }
      }else{
      //     .
      }    
  }

  const checkVaildate = () =>{
    return name.length > 2 && phone.length > 9
   }

   useEffect(()=>{
     console.log('useEffect( ):',name,phone,cernum,verify_cernum,isceo);
     if(checkVaildate())
       setActive(true);
     else
       setActive(false);
   },)

    //     
    const nextStep = async (e) => {
      e.preventDefault();
      console.log('nextStep   a :',e,e.target);
       
      // (),  () ,    ()   cernum    row .
      let body_info = {
        phone_number : phone, 
        cernum_number : cernum
      }
      var cernumchk_validate_result = await serverController.connectFetchController('/api/cernum_validate_process','POST',JSON.stringify(body_info));

      if(cernumchk_validate_result){
        console.log('cernumchk_validate_resultss::',cernumchk_validate_result);
        
        if(cernumchk_validate_result.success){
          // 

          setActive2(true);

          tempRegisterUserdataActions.namechange({names:name});
          tempRegisterUserdataActions.phonechange({phones:phone});

          history.push('/AgencyJoinInfo');
        }else{
          // 
          setActive2(false);

          alert(cernumchk_validate_result.message);
        }
      }
    }

    return (
        <Container>
          <TopTxt>     .</TopTxt>
          <WrapChooseBox>
            <ChooseBox>
              <Checkbox type="radio" name="company" id="check1" className='is_agency_ceo' onChange={is_agency_ceo_changes}/>
                <Label for="check1" className="chk_label">
                  <Span className="chk_on_off"></Span>
                    .
                </Label>
            </ChooseBox>
            <ChooseBox>
              <Checkbox type="radio" name="company" id="check2" className='is_agency_ceo' onChange={is_agency_ceo_changes}/>
              <Label for="check2" className="chk_label">
                <Span className="chk_on_off"></Span>
                   .
              </Label>
            </ChooseBox>
          </WrapChooseBox>

  {/*      .( WrapChooseBox display:none )*/}
          <WrapJoinInput>
            <InputTop>
              <InputTitle></InputTitle>
              <Input type="text" name="" placeholder=" ." onChange={nameChange}/>
              <InputTitle></InputTitle>
              <Input type="text" name="" placeholder=" '-' ." onChange={phoneChange}/>
              {/*NextBtn()   show*/}
              <InputCerNum type="text" name="" placeholder=" ." id='inputcernum' onChange={cernumChange} style={{display:"none"}}/>
              {/*   Msg*/}
              <ErrorMsg style={{display:"none"}}>   .</ErrorMsg>
            </InputTop>
            <SubmitButton>
                <NextBtn type="button" name="" active={active} onClick={aligoSmsSend}> </NextBtn>
                {/*NextBtn()  show*/}
                {/*  1)     (   MemJoinAgree  .)    flow  .*/}
                <Link to="/AgencyJoinAgree">
                  <Submit type="submit" name="" className='nextButtons' active2={active2} id='agencyJoinAgree_move' style={{display:"none"}} onClick={nextStep}></Submit>
                </Link>
                {/*  2)    (     ) */}
                <Link to="/AgencyJoinInfo">
                  <GoNextPage type="submit" name="" className='nextButtons' active2={active2} id='agencyJoinInfo_move' style={{display:"none"}} onClick={nextStep}></GoNextPage>
                </Link>
            </SubmitButton>
          </WrapJoinInput>
        </Container>
  );
}

const Pb = styled.b`
  display:block;
  @media ${(props) => props.theme.mobile} {
        display:inline;
    }
`
const Mb = styled.b`
  display:inline;
  @media ${(props) => props.theme.mobile} {
        display:block;
    }
`

const Container = styled.div`
    width:450px;
    margin:0 auto;
    padding-top:16px;
    @media ${(props) => props.theme.mobile} {
        width:calc(100vw*(370/428));
        padding-top:calc(100vw*(0/428));
      }
`
const TopTxt = styled.h2`
  font-size:15px;
  font-weight:800;
  transform:skew(-0.1deg);
  text-align:center;
  margin-bottom:80px;
  @media ${(props) => props.theme.mobile} {
      font-size:calc(100vw*(13/428));
      margin-bottom:calc(100vw*(40/428));
    }
`
const WrapChooseBox = styled.div`
  width:100%;
  background:#f7f8f8;
  padding:70px 0px 70px 100px;
  @media ${(props) => props.theme.mobile} {
      width:calc(100vw*(345/428));
      margin:0 auto;
      padding:calc(100vw*(70/428)) 0 calc(100vw*(40/428)) calc(100vw*(70/428));
    }
`
const ChooseBox = styled.div`
  width:100%;
  margin-bottom:15px;
  @media ${(props) => props.theme.mobile} {
      margin-bottom:calc(100vw*(35/428));
    }
`
const Checkbox = styled.input`
  display:none;
  &:checked + .chk_label .chk_on_off {
    width:16px;height:16px;background:url(${Checked}) no-repeat;background-size:100% 100%;
    }
    @media ${(props) => props.theme.mobile} {
      &:checked + .chk_label .chk_on_off {width:calc(100vw*(15/428));height:calc(100vw*(15/428));background-size:100% 100%}
      }
`
const Label = styled.label`
  font-size:15px;
  font-weight:500;transform:skew(-0.1deg);
  color:#4a4a4a;
  font-family:'nbg',sans-serif;
  @media ${(props) => props.theme.mobile} {
    font-size:calc(100vw*(14/428));
    }
`
const Span = styled.span`
  width:16px;height:16px;display:inline-block;
  margin-right:24px;vertical-align:middle;
  background:url(${Check}) no-repeat; background-size:100% 100%;
  @media ${(props) => props.theme.mobile} {
    width:calc(100vw*(15/428));height:calc(100vw*(15/428));
    margin-right:calc(100vw*(20/428));
    }
`

const WrapJoinInput = styled.div`
    width:410px;
    margin:0 auto;
    padding-bottom:150px;
    @media ${(props) => props.theme.mobile} {
        width:100%;
        padding-bottom:calc(100vw*(100/428));
      }
`
const InputTop = styled.div`
    position:relative;
    width:100%;
    padding-bottom:60px;
    @media ${(props) => props.theme.mobile} {
        padding-bottom:calc(100vw*(50/428));
      }
`
const InputTitle = styled.label`
    display:inline-block;
    font-size:12px;
    padding-left:7px;
    margin-bottom:10px;
    font-weight:600;
    transform:skew(-0.1deg);
    @media ${(props) => props.theme.mobile} {
        font-size:calc(100vw*(12/428));
        padding-left:calc(100vw*(7/428));
        margin-bottom:calc(100vw*(9/428));
      }
`
const Input = styled.input`
  width:100%;
  height:43px;
  transform:skew(0.1deg);
  font-weight:600;
  font-size:15px;
  margin-bottom:15px;
  color:#4a4a4a;
  text-align:center;
  border-radius:4px;
  border:1px solid #e4e4e4;
  &:nth-child(4){margin-bottom:0;}
  &::placeholder{color:#979797;}
  @media ${(props) => props.theme.mobile} {
      height:calc(100vw*(43/428));
      font-size:calc(100vw*(14/428));
      margin-bottom:calc(100vw*(15/428));
    }
`
const InputCerNum = styled(Input)`
  margin-top:10px;
  @media ${(props) => props.theme.mobile} {
      margin-top:calc(100vw*(10/428));
    }
`

const SubmitButton = styled.div`
  width:100%;
`
const NextBtn = styled.button`
  width:100%;
  height:66px;
  line-height:60px;
  font-size:20px;
  color:#fff;
  border-radius:11px;
  border:3px solid #e4e4e4;
  transition:all 0.3s;
  font-weight:800;
  background:${({active}) => active ? "#01684b" : "#979797"};
  border:${({active}) => active ? "3px solid #04966d" : "3px solid #e4e4e4"};
  @media ${(props) => props.theme.mobile} {
      height:calc(100vw*(60/428));
      line-height:calc(100vw*(54/428));
      font-size:calc(100vw*(15/428));
    }
`
const Submit = styled(NextBtn)`
  background:${({active2}) => active2 ? "#01684b" : "#979797"};
  border:${({active2}) => active2 ? "3px solid #04966d" : "3px solid #e4e4e4"};
`
const GoNextPage = styled(Submit)`
  background:${({active2}) => active2 ? "#01684b" : "#979797"};
  border:${({active2}) => active2 ? "3px solid #04966d" : "3px solid #e4e4e4"};
`
const ErrorMsg = styled.p`
  position:absolute;
  left:0;
  bottom:30px;
  width:100%;
  font-size:12px;color:#fe0101;
  font-weight:600;transform:skew(-0.1deg);
  text-align:center;
  @media ${(props) => props.theme.mobile} {
      bottom:calc(100vw*(18/428));
      font-size:calc(100vw*(12/428));
    }
`
