//react
import React ,{useState, useEffect} from 'react';
import {Link} from "react-router-dom";


//css
import styled from "styled-components"

//img
import Filter from '../../../../img/member/filter.png';
import Bell from '../../../../img/member/bell.png';
import BellActive from '../../../../img/member/bell_active.png';
import Location from '../../../../img/member/loca.png';
import Set from '../../../../img/member/setting.png';
import Item from '../../../../img/main/item01.png';
import Noimg from '../../../../img/member/company_no.png';
import Close from '../../../../img/main/modal_close.png';
import Change from '../../../../img/member/change.png';
import Marker from '../../../../img/member/marker.png';
import ArrowDown from '../../../../img/member/arrow_down.png';

import { Mobile, PC } from "../../../../MediaQuery"

import serverController from '../../../../server/serverController';


//component
import RequestListPage from "./RequestList";
import RequestSorting from "./RequestSorting";
import ModalAddUserInfo from './modal/ModalAddUserInfo';
import CommonTopInfo from '../../../../component/member/mypage/commonList/commonTopInfo';

//redux addons assets;
import {useSelector } from 'react-redux';


export default function Request({mannerModal,startModal,filterModal,cancleModal,completeModal,cancle2Modal,setFilter,value,type}) {

   
  //... ()
  const [menu,setMenu] = useState(false);
  const showModal =()=>{
    setMenu(!menu);
  }
  const [brokerproductlist,setBrokerproductlist]= useState([])
  const [is_serveron,setIs_serveron] = useState(false);

  const [userInfo,setUserInfo] = useState(false);

  const login_user_redux = useSelector(data => data.login_user);//   . mem_id.
  console.log('myRequest     , ( products): .',login_user_redux);
  
  useEffect( async () => {
     //  /     .    prd_id  request_memid    (  ) prd_idneity_id distinct()   mem_id.
     /*let login_res=await serverController.connectFetchController('/api/auth/islogin','get');
    console.log('islogin request result myrequest>>>',login_res,login_res.login_session);
    */
    let body_info ={
       user_type : login_user_redux.user_type,//      ,    companyid ,  comapnyid  memid rewqustememid  .
    };
    console.log('>>>>post parameter list:',body_info);
    try{
      let res=await serverController.connectFetchController('/api/broker/user_brokerRequestlistview','post',JSON.stringify(body_info));
      console.log('res_result:',res);
      //alert(res);

      if(res){
        setIs_serveron(true);
        if(res.success){
          if(res.result_data){
            setBrokerproductlist(res.result_data);

          }
        }

      }else{
        setIs_serveron(false);
      }

    }catch(e){

    }
  },[]);//    .
  console.log('-===>>>page load broerk_proudctlist value:',brokerproductlist);

  /*data map*/
  const RequestListItem =[
    {
      prd_identity_id : 0,
      prd_img:Item,
      date:"21.00.00 - 21.00.00",
      prd_status:" ",
      modify_date:"2021.00.00",
      prd_name:"2",
      prd_type:"",
      address:" 3 203    (OO OO)",
      address_detail: " 3 203    (OO OO)",
      prd_sel_type:"",
    },
    {
      prd_identity_id : 1,
      prd_img:Item,
      date:"21.00.00 - 21.00.00",
      prd_status:" ",
      modify_date:"2021.00.00",
      prd_name:"2",
      prd_type:"",
      address:" 3 203    (OO OO)     ",
      address_detail:" 3 203    (OO OO)     ",
      prd_sel_type:""
    },
    {
      prd_identity_id : 2,
      prd_img:Noimg,
      date:"21.00.00 - 21.00.00",
      prd_status:" ",
      modify_date:"2021.00.00",
      prd_name:"2",
      prd_type:"",
      address:" 3 203    (OO OO)",
      address_detail:" 3 203    (OO OO)",
      prd_sel_type:"",
    },
    {
      prd_identity_id : 3,
      prd_img:Noimg,
      date:"21.00.00 - 21.00.00",
      prd_status:" ",
      modify_date:"2021.00.00",
      prd_name:"2",
      prd_type:"",
      address:" 3 203    (OO OO)",
      address_detail:" 3 203    (OO OO)",
      prd_sel_type:"",
    }
]

  const topInfoContent = () => {
    return(
      <FilterAndAdd>
      {/*       */}
      
      {/**/}

      {/*05.21        !! default  */}
      {
        !login_user_redux.user_name || !login_user_redux.phone ?
          <div className="cursor-p" onClick={()=>{setUserInfo(true)}}>
          <AddBtn></AddBtn>
         </div>
         :
         <Link to="/AddRequest" className="cursor-p">
           <AddBtn></AddBtn>
         </Link>
      }
      
    </FilterAndAdd>
    )
  }

    return (
        <Container>
          <WrapRequest>
            <TopTitle> </TopTitle>
            <RequestSorting filterModal={filterModal}/>{/**/}

            {/* . */}
            <CommonTopInfo length={is_serveron == true ? brokerproductlist.length : RequestListItem.length} leftComponent={topInfoContent()}/>
            {/* --  . */}
            {/*
              <TopInfo>
                <All> <GreenColor>{ is_serveron == true ? brokerproductlist.length : RequestListItem.length}</GreenColor> </All>
                <FilterAndAdd>
                  <Link to="/AddRequest">
                    <AddBtn></AddBtn>
                  </Link>
                </FilterAndAdd>
              </TopInfo>
            */}

            <RequestList>
              {console.log('server logon or error :',is_serveron)}
            {
              /*
            is_serveron == true &&  RequestListItem.length > 0 ?  //false -> true ...
            RequestListItem.map((value) => {

              const type=()=>{
                if(value.prd_status == " ") { //
                  return 1
                }else if(value.prd_status == " ") {//
                  return 1
                } else if(value.prd_status == " ") { //  
                  return 0.5
                } else if(value.prd_status == " ") { //  
                  return 0.5
                }
              }

              return(
                <RequestListPage filterModal={filterModal} mannerModal={mannerModal} cancleModal={cancleModal} startModal={startModal} cancle2Modal={cancle2Modal} completeModal={completeModal} type={type} value={value}/>
              )
            })
            :
            null
            */
          }
          
          {
            is_serveron == true && brokerproductlist.length > 0 ? 
            brokerproductlist.map((value) => {

              let local_item= value['prd_id_history_child'][0];//prdidienitiyid     0  origin(x,y    )       .
              let match_transaction_item=value['match_transaction_row'][0];//  prdiientityi   transaciton .
              const type=()=>{
                if(match_transaction_item.prd_status == '' || match_transaction_item.prd_status==''){
                  return 1
                }else if(match_transaction_item.prd_status == ''){
                  return 1
                }else if(match_transaction_item.prd_status == ''){
                  return 0.5
                }else if(match_transaction_item.prd_status == ''){
                  return 0.5
                }
              }

              return(
                <RequestListPage setFilter={setFilter} type={type} value={local_item} match_transaction_item={match_transaction_item} filterModal={filterModal} mannerModal={mannerModal} cancleModal={cancleModal} startModal={startModal} cancle2Modal={cancle2Modal} completeModal={completeModal}/>
              )
              
            })
            :null
          }

        </RequestList>
      </WrapRequest>
      
          {
            userInfo ?
            <ModalAddUserInfo setUserInfo={setUserInfo}/>
            :
            null
          }
  </Container>
  );
}

const Pb = styled.b`
  display:block;
  @media ${(props) => props.theme.mobile} {
        display:inline;
    }
`
const Mb = styled.b`
  display:inline;
  @media ${(props) => props.theme.mobile} {
        display:block;
    }
`
const Container = styled.div`
    width:680px;
    margin:0 auto;
    padding:24px 0 250px;
    @media ${(props) => props.theme.mobile} {
      width:calc(100vw*(380/428));
      padding:calc(100vw*(30/428)) 0 calc(100vw*(150/428));
      }
`
const WrapRequest = styled.div`
  width:100%;
`
const TopTitle = styled.h2`
  font-size:20px;color:#707070;
  text-align:left;padding-left:30px;
  font-weight:800;transform:skew(-0.1deg);
  @media ${(props) => props.theme.mobile} {
    font-size:calc(100vw*(14/428));
    padding-left:calc(100vw*(16/428));
    }
`
const TopInfo = styled.div`
  display:flex;justify-content:space-between;align-items:center;
  padding:16px 40px;
  margin-top:30px;
  border-top:1px solid #f2f2f2;
  border-bottom:1px solid #f2f2f2;
  @media ${(props) => props.theme.mobile} {
    margin-top:calc(100vw*(30/428));
    padding:calc(100vw*(22/428)) calc(100vw*(10/428));
    }
`
const All = styled.span`
  font-size:17px;color:#4a4a4a;
  font-weight:800;transform:skew(-0.1deg);
  @media ${(props) => props.theme.mobile} {
    font-size:calc(100vw*(14/428));
    }
`
const FilterAndAdd = styled.div`
  position:relative;
  display:flex;justify-content:flex-start; align-items:center;
`
const AddBtn = styled.div`
  width: 81px;
  height: 30px;
  border-radius: 4px;
  border: solid 2px #f0a764;
  background-color: #fe7a01;
  line-height:26px;
  font-size:13px;
  font-weight:800;transform:skew(-0.1deg);
  text-align:center;
  margin-left:15px;
  color:#fff;
  @media ${(props) => props.theme.mobile} {
    width:calc(100vw*(80/428));
    height:calc(100vw*(30/428));
    line-height:calc(100vw*(26/428));
    font-size:calc(100vw*(13/428));
    margin-left:calc(100vw*(15/428));
    }
`
const GreenColor = styled(All)`
  color:#01684b;
`
const FilterImg = styled.img`
  display:inline-block;
  width:18px;
  @media ${(props) => props.theme.mobile} {
    width:calc(100vw*(18/428));
  }
`
const RequestList = styled.ul`
  width:100%;
`
