//const ip = "http://localhost:8080/";
const api = {
  connectFetchController : async (path,method,body,callBack,errorCallBack) =>{
     console.log('connectFetchController:',path,method,body);
     return fetch(`${path}`, {
        credentials:'include',
        method: method,
        body:body?body:null,
        
      }).then(function(res) {
         console.log('   :',res);
        return res.json();
      }).then(function(data) {
        if(callBack)
          callBack(data);
        return data;
      }).catch(function(e){
        if(errorCallBack)
          errorCallBack(e);
      });
    },
}

export default api;