import React ,{useState, useEffect} from 'react';
import DaumPostcode from 'react-daum-postcode';

export default function addressSearchApi({setSearch_address,setAddressApi,setAddressroad,setAddressjibun}) {

  const handleComplete = (data) => {
    let fullAddress = data.address;
    let extraAddress = '';
    console.log(' api data type::',data);

    if (data.addressType === 'R') {
      if (data.bname !== '') {
        extraAddress += data.bname;
      }
      if (data.buildingName !== '') {
        extraAddress += (extraAddress !== '' ? `, ${data.buildingName}` : data.buildingName);
      }
      fullAddress += (extraAddress !== '' ? ` (${extraAddress})` : '');
    }

    console.log(fullAddress);  // e.g. '  2 20 (1)'

    var match_sido_info={
      '' : '',
      '' : '',
      '' : '',
      '' : '',
      '' : '',
      '' : '',
      '' : '',
      '' : '',
      '' : '',
      '' : '',
      '' : '',
     '' : '',
     '' : '',
     '' : '',
     '' : '',
     '' : ''
    }
    let sido=data.sido;
    let jibunaddress=data.jibunAddress.replace(sido,match_sido_info[sido]);//sido     .
    let roadaddress=data.roadAddress.replace(sido,match_sido_info[sido]);
    let query = data.query;
    setSearch_address({query_val:query, fulladdress_val:fullAddress,sido_val:data.sido,sigungu_val:data.sigungu,jibunaddress_val:jibunaddress,roadaddress_val:roadaddress});
    setAddressApi(false);
    setAddressroad(roadaddress);
    setAddressjibun(jibunaddress);
  }

    return (
        <div>
          <DaumPostcode
            onComplete={handleComplete}
          />
        </div>
  );
}
