export default function ModalReserve({ except_datelist, result_usedatalist, reserve,setReserve, sendInfo_local, tr_id}) {
  // 
  const [timeSelect,setTimeSelect] = useState(false);
  const [selectDay,setSelectDay] = useState(null);
  const [timeList,setTimeList] = useState([]);
  const [tourid,setTourid] = useState('');
  const [tourtype,setTourtype] = useState('');
  const [selecttdid,setSelecttdid] = useState('');

  var now_date=new window.Date();
  now_date.setHours(0);now_date.setMinutes(0);now_date.setSeconds(0);now_date.setMilliseconds(0);
  console.log('now_date hmmmsss:',now_date);

  const showSelect = ()=>{setTimeSelect(!timeSelect);}
 
  const [activeIndex,setActiveIndex] = useState(0);  //slick slider setting

  var settings = {
    dots: false,
    infinite: false,
    speed: 500,
    slidesToShow: 5,
    slidesToScroll: 5,
  };

  console.log('=>>>>modalReserver  >>:',except_datelist,result_usedatalist);
  //console.log(except_datelist);
  //console.log(result_usedatalist);
  const [serverload_timelist,setServerload_timelist] = useState([]);
  const [serverload_tr_info,setServerload_tr_info] = useState({});

  useEffect( async () => {
    let body_info={
      tr_id_val : tr_id
    };
    let res=await serverController.connectFetchController('/api/broker/brokerProduct_reservationRegisterview','POST',JSON.stringify(body_info));

    if(res){
      console.log('server request resultss:',res);

      var result_data=res.result_data;
      var td_detail_list=[];

      var tr_info={};
      
      for(let r=0; r<result_data.length; r++){
        tr_info['r_td_id']=result_data[r].r_td_id;// tr_Id  td_id  , tour_id   tour_id  .selected,checked.
        tr_info['r_tour_id']=result_data[r].r_tour_id;
        tr_info['reserv_start_time'] = result_data[r].reserv_start_time;
        tr_info['reserv_end_time'] = result_data[r].reserv_end_time;
        tr_info['r_tour_start_date']=result_data[r].t_tour_start_date;
        tr_info['r_tour_type'] = result_data[r].t_tour_type;
        tr_info['r_tour_reservDate'] = result_data[r].r_tour_reservDate;//.

        td_detail_list[r] = {};
        td_detail_list[r]['td_id']=result_data[r].td_td_id;
        td_detail_list[r]['td_text']=result_data[r].td_td_text;//  tdid,td_text .

        if(result_data[r].td_td_text =='1T'){
          td_detail_list[r]['td_starttime']='9:00am';
          td_detail_list[r]['td_endtime']='12:00pm';
        }else if(result_data[r].td_td_text == '1T'){
          td_detail_list[r]['td_starttime']='12:00pm';
          td_detail_list[r]['td_endtime']='15:00pm';
        }else if(result_data[r].td_td_text == '2T'){
          td_detail_list[r]['td_starttime']='15:00pm';
          td_detail_list[r]['td_endtime']='18:00pm';
        }
        
      }
      setServerload_tr_info(tr_info);
      setServerload_timelist(td_detail_list);

      console.log('-->>>page load set reserv start,end times::',tr_info,tr_info['reserv_start_time'],tr_info['reserv_end_time']);
      //setStartTime(new Date(tr_info['reserv_start_time']));
      //setEndTime(new Date(tr_info['reserv_end_time']));

      setSelectDay(tr_info['r_tour_reservDate']); setTourid(tr_info['r_tour_id']); setTourtype(tr_info['r_tour_type']); setSelecttdid(tr_info['r_td_id']);
      // state, seninfo_local .
      sendInfo_local(tr_info['r_tour_reservDate'],tr_info['r_td_id'],tr_info['r_tour_id'],tr_info['r_tour_type'],tr_info['r_td_id'],tr_id);   
    }

  },[]);

  const change_dateEvent = async (date,setTimes,tour_id,tour_type,selecttdidval) => {
    console.log('  ,  index',date,setTimes, activeIndex, tour_id, tour_type);

    setSelectDay(date);
    //setTimeList(setTimes.split(','));
    setTourid(tour_id);
    setTourtype(tour_type);

    //change_dateEvent    tour_id    .   .    tour_id .  tourid( )     .       tourid.( )    tdid()    .
    let body_info={
      tour_id_val : tour_id
    };
    let res= await serverController.connectFetchController('/api/broker/brokerProduct_tourid_tourdetailList','POST',JSON.stringify(body_info));
    console.log('server request result:',res);

    var tdDetail_list=[];
    for(var ss=0; ss<res['result_data'].length; ss++){
      let result_data_item=res['result_data'][ss];
      tdDetail_list[ss] ={};
      tdDetail_list[ss]['td_text'] = result_data_item['td_text'];
    
      var tdDetail_starttime_val; var tdDetail_endtime_val;
      if(tdDetail_list[ss]['td_text'] == ' 1T' || tdDetail_list[ss]['td_text']=='1T'){
         tdDetail_starttime_val = '9:00am';
         tdDetail_endtime_val = '12:00pm';
      }else if(tdDetail_list[ss]['td_text'] == ' 1T' || tdDetail_list[ss]['td_text'] == '1T'){
        tdDetail_starttime_val = '12:00pm';
        tdDetail_endtime_val = '15:00pm';
      }else if(tdDetail_list[ss]['td_text'] == ' 2T' || tdDetail_list[ss]['td_text'] == '2T'){
        tdDetail_starttime_val = '15:00pm';
        tdDetail_endtime_val = '18:00pm';
      }
      tdDetail_list[ss]['tour_id'] = result_data_item['tour_id'];
      tdDetail_list[ss]['td_id'] = result_data_item['td_id'];// timeDetail.    tdid?
      tdDetail_list[ss]['td_starttime'] = tdDetail_starttime_val;
      tdDetail_list[ss]['td_endtime'] = tdDetail_endtime_val;

      setTimes=result_data_item['td_id'];
      
    }
    console.log('==>>>>tdDetail_list:',tdDetail_list);
    setTimeList(tdDetail_list);
    setServerload_timelist(tdDetail_list);

    console.log(' datechange:',date,setTimes,tour_id,tour_type,setTimes,tr_id);
    sendInfo_local(date,setTimes,tour_id,tour_type,setTimes,tr_id);
  }
  if(reserve == false)
    return null;
    return (
        <Container>
          <WrapModalMap>
              <WrapTourDate>
                <TourTitle> trid:{tr_id}</TourTitle>
                  <Slider {...settings} className="about">
                    {
                      result_usedatalist.map((value , index) => {
                        
                        let value_datetime=new window.Date(value.date);
                        let from_now;
                        if(value_datetime.getTime() >= now_date.getTime()){
                          //    .
                          from_now = true;
                        }else{
                           from_now =false;//     .( .  )
                        }
                        
                        console.log('=>>>>>result_useitemList: and nowDate',value,index,now_date);

                       if(!value['isexcepted'] && from_now == true){
                         //       .      ( )            ,     ,        .
                        return(
                          <SlickSlide className="slide__one">
                            <Link>
                              <WrapDateItem>
                                <DateItem serverload_active={serverload_tr_info['r_tour_reservDate']==value['date']} active={activeIndex == index} onClick={() => { change_dateEvent(value['date'],value['setTimes'],value['tour_id'],value['tour_type'],selecttdid); setActiveIndex(index)}}>
                                    <Day>{value['date_yoil']}({value['tour_type']=='1'?'':''})</Day>
                                    <Date>{value['date_day']}</Date>
                                </DateItem>
                              </WrapDateItem>
                            </Link>
                          </SlickSlide>
                        )
                       }
                        
                      })
                    }
                   
                  </Slider>
                </WrapTourDate>
                <FilterBox>
                  <FilterSelectSort>
                    <FilterSelectSortList id='select_setTimes' onChange={(e)=>{ 
                      console.log('=>>>  >>');
                      console.log('>>>selectaday():',selectDay);
                      console.log('>>>timelist():',serverload_timelist);
                      console.log('>>>>tourid, tourtype:',tourid,tourtype);
                      console.log('>>>(td_id):',e.target.value);

                      //setReservationId({selectDate:selectDay,selectTime:e.target.value, selectTourid:tourid, selectTourtype: tourtype, selectTdid:e.target.value}); //reserationId state      insert    .50%
                      sendInfo_local(selectDay,e.target.value,tourid,tourtype,e.target.value,tr_id);//,,,  ..
                      setTimeSelect(e.target.value);
                    }}>
                      <InOption selected disabled> .</InOption>
                      {
                        serverload_timelist.map((value)=>{
                          console.log('value hmm:',value);
                          return  <InOption value={value['td_id']} selected={serverload_tr_info['r_td_id']==value['td_id']}>{value['td_text']}({value['td_starttime']}~{value['td_endtime']})</InOption>
                        })
                      }
                    </FilterSelectSortList>
                  </FilterSelectSort>
                </FilterBox>
          </WrapModalMap>
        </Container>
  );
}

