//react
import React ,{useState, useEffect, useRef} from 'react';
import {Link} from "react-router-dom";

//css
import styled from "styled-components"
import NavIcon from '../../../img/main/nav_btn.png';
import Logo from '../../../img/main/header_logo.png';
import PCLogo from '../../../img/main/pc_header_logo.png';
import Mypage from '../../../img/main/mypage_icon.png';
import ScrollLoader  from '../../../img/map/scrollLoader.gif';
import ScrollLoader2  from '../../../img/map/scrollLoader2.gif';

// components
import { Mobile, PC } from "../../../MediaQuery";
import MainSideBar from './MainSideBar';
import SideBarItemDetail from './SideBarItemDetail';
import SideBarBrokerDetail from './SideBarBrokerDetail';
import SideBarDanjiDetail from './SideBarDanjiDetail';

// redux
import { MapProductEls } from '../../../store/actionCreators';
import { useSelector } from 'react-redux';

import ModalCommon from '../../../component/common/modal/ModalCommon';
import ModalReserve from '../../../component/member/mypage/reservation/ModalReserve';

//server process
import serverController from '../../../server/serverController';

export default function WrapSideBar({ containerRef, setReport,pageIndex,setPageIndex,reserveModal, status, setMap, setDangimap_data}) {
  // console.log('mainWrpasidebar>>>');
  //   
  // const [pageIndex , setPageIndex] = useState(0);
  const [historyInfo , setHistoryInfo] = useState({pageIndex:1,prevTab:"",prevIndex:[]});
  const [updown,setUpDown] = useState(false);
  const [click_prdidentityid,setClick_prdidentityid] = useState('');//  ()
  const [click_complexid, setClick_complexid] = useState('');//  (->  )

  const [reservationId,setReservationId] = useState({id:0 ,text:""});
 
  const [activeIndex,setActiveIndex] = useState(0);//  tab 0:,, .

  const productRedux = useSelector(state=>{ return state.mapProductEls});
  const mapRightRedux = useSelector(state=>{ return state.mapRight});
  const login_userinfo= useSelector(data => data.login_user);

  // console.log(productRedux.clickmarker);

  const loaderRef = useRef();

  //const [ismarkerclick,setIsmarkerclick] = useState(productRedux.clickmarker);
  
  // const containerRef = useRef();
  
  const position=()=>{
    if(updown == true) {
      return "absolute"
    }else{
      return "relative"
    }
  }
  const overflow=()=>{
    if(updown == true) {
      return "scroll"
    }else{
      return "hidden"
    }
  }
  const top=()=>{
    if(updown == true) {
      if(mapRightRedux.isExclusive.is){
        return "calc(100vw*(59/428));"
      }else{
        return "calc(100vw*(0/428));"
      }
    }else{
      return "calc(100vw*(-122/428));"
    }
  }
  
  const sendinfo_data={};
  const sendInfo_local = (selectDay,selectTimes,tourid,tourtype, td_id,company_id) => {
    sendinfo_data['selectDay'] = selectDay;  //.  
    sendinfo_data['selectTimes'] = selectTimes;  // .tdid.  tourid   tdid()
    sendinfo_data['tourid'] = tourid;  //()
    sendinfo_data['tourtype'] = tourtype; //(, )
    sendinfo_data['td_id']= td_id;  // tdid.
    sendinfo_data['company_id'] = company_id;// id
    console.log('senfInfo_local   >>>>:',sendinfo_data);
  }

  const pageLoader = (updateReserveModals) =>{
    switch (pageIndex) {
      case 0: return <MainSideBar status={status} setActiveIndex={setActiveIndex} activeIndex={activeIndex} updatePageIndex={updatePageIndex} historyInfo={historyInfo} setHistoryInfo={setHistoryInfo} updown={updown} setUpDown={setUpDown} containerRef={containerRef}/>;
      case 1: return <SideBarItemDetail updatePageIndex={updatePageIndex} historyInfo={historyInfo} setHistoryInfo={setHistoryInfo} setReport={setReport} updateReserveModal={updateReserveModals} click_prdidentityid={click_prdidentityid}/>; // 
      case 2: return <SideBarBrokerDetail updatePageIndex={updatePageIndex} historyInfo={historyInfo} setHistoryInfo={setHistoryInfo}/>;// 
      case 3: return <SideBarDanjiDetail click_complexid={click_complexid} updatePageIndex={updatePageIndex} historyInfo={historyInfo} setHistoryInfo={setHistoryInfo} setMap={setMap} setDangimap_data={setDangimap_data}/>;//   
      default :return <MainSideBar status={status} setActiveIndex={setActiveIndex} activeIndex={activeIndex} updatePageIndex={updatePageIndex} historyInfo={historyInfo} setHistoryInfo={setHistoryInfo} updown={updown} setUpDown={setUpDown}/>;
    }
  }
  const updatePageIndex = (index,id) =>{
    if(index < 0){
      setPageIndex(0);
    }
    else if(index == 1){
      setPageIndex(1);
      setClick_prdidentityid(id); // prd_idnentity mainWrapsidebar state .
    }
    else if(index == 2){
      setPageIndex(2);
    }
    else if(index == 3){
      setPageIndex(3);
      setClick_complexid(id);
    }
    else{
      setPageIndex(index);
    }
  }

  //   
  const [reserve,setReserve] = useState(false);
  
  const [modalOption,setModalOption] = useState({show: false,setShow: null, link:'',title:'',submit:{},cancle:{},confirm:{},confirmgreen:{},content:{}});

  const offModal = () => {
    let option = JSON.parse(JSON.stringify(modalOption));
    option.show =false;
    setModalOption(option);
  }

  const clickReservation = async () =>{
    // console.log('reservationId( ):',sendinfo_data);
    
    offModal();
    
    if(sendinfo_data.selectDay  && sendinfo_data.selectTimes && sendinfo_data.tourid && sendinfo_data.tourtype && sendinfo_data.td_id){
      if(login_userinfo.is_login){
        let body_info= {
          selectdate : sendinfo_data.selectDay, //(    )
          selectTime: sendinfo_data.selectTimes, //(tourid:)  tdid
          slectTourid : sendinfo_data.tourid, //tourid
          selectTourtype : sendinfo_data.tourtype,  //tourtype
          selectTdid: sendinfo_data.td_id, //tdid
          phone: login_userinfo.phone, 
          email: login_userinfo.email,
          user_name : login_userinfo.user_name,
          user_type : login_userinfo.user_type,
        };
        // / ,    tourId() (:,), (,)  .
        // console.log('JSON_BODY>>>:',JSON.stringify(body_info));
        let res= await serverController.connectFetchController('/api/broker/brokerProduct_tourReservation_register','POST',JSON.stringify(body_info));
        if(res){
          // console.log('====>>>res::',res);

          if(res.success){
            alert('!');

            //      .companyid .
            let noti_info = {
              prd_identity_id : click_prdidentityid,//  
              //tour_reserve_memid : login_userinfo.mem_id,
              message : click_prdidentityid+'    .\n [ ]:'+login_userinfo.user_name+'('+login_userinfo.phone+')'+login_userinfo.email,
              company_id : sendinfo_data.company_id,
              noti_type : 9 //.
            }
            console.log('send notifino::',noti_info);
            let noti_res = await serverController.connectFetchController('/api/alram/notification_process','POST',JSON.stringify(noti_info));
            if(noti_res){
              if(noti_res.success){
                console.log('noti_resss :',noti_res);
              }else{
                alert(noti_res.message);
              }
            }

          }else{
            alert('  .');
          }
        }
      }else{
        alert('  .');
      }
    }else{
      alert(' !');
    }
  }

  const updateReserveModal = (except_datelist,result_usedatalist,company_id) =>{
    // console.log('updateReservemodal  >>>     / dateList:',except_datelist,result_usedatalist,company_id);
    setModalOption({
        show:true,
        setShow:offModal,
        title:" ",
        content:{type:"component",text:``,component:<ModalReserve sendInfo_local={sendInfo_local} company_id={company_id} setReservationId={setReservationId} except_datelist={except_datelist} result_usedatalist={result_usedatalist}/>},
        submit:{show:false , title:"" , event : ()=>{offModal(); }},
        cancle:{show:false , title:"" , event : ()=>{offModal(); }},
        confirm:{show:true , title:"" , event : clickReservation }
    });
  }

  //  
  // **api      .
  const onScrollList = async () => {
    // console.log('====>>onScorllList>>>');
    // console.log('===>>currsent.scrollheight,scrolltop,.clientheight:',containerRef.current.scrollHeight,containerRef.current.scrollTop,containerRef.current.clientHeight);

    if(containerRef.current.scrollHeight <= containerRef.current.scrollTop+containerRef.current.clientHeight + 50){
      //   ||   --------------
      if(pageIndex == 0){

        console.log('activeIndex::',activeIndex);

        if(activeIndex == 0){
          // active   .
          if(mapRightRedux.isExclusive.is){
            const currentArr = JSON.parse(JSON.stringify(productRedux.exclusive));
            //    currentarr exclusive .          index.
            console.log('mapRight active,  onscroll ::',status);
            console.log(' currentArr:',currentArr,currentArr.length);//   mapData ??mapdata (idle ,   ,  searchdata origin .)   , 1. (,,) 2.     3.searchdata or mapdata     .    ,currentArr()    .
            
            loaderRef.current.style.display='flex';
            var scrollevent_server_async_request= async function(){
              
              let searchdetail_origindata = JSON.parse(localStorage.getItem("searchdetail_origin"));
              let mapData = JSON.parse(localStorage.getItem("mapData"));
              console.log('scrollevent    >>',searchdetail_origindata,mapData, currentArr);//   searchdetail .
    
              if(mapData){
                var send_info = {
                   next_index : currentArr.length,
                   level : mapData.level, //mapdata   
                   lat : mapData.lat,
                   lng : mapData.lng,
                   screen_width : window.innerWidth,//
                   screen_height : window.innerHeight,
                   prd_type : status,//  (,,,) .
  
                }
              }else{
                console.log('====>>mapData  !!!!()=-========');
                return;
              }
              
              let exculsive_data_more = await serverController.connectFetchController("/api/matterial/nextExclusive_item_sidebar",'POST',JSON.stringify(send_info));
              console.log('===>>exculisve_data_more result::',exculsive_data_more);
    
              return exculsive_data_more;
            };
            var await_data_get = await scrollevent_server_async_request();
            console.log('await data getss::>>',await_data_get);
            
            if(await_data_get){
              if(await_data_get.result){
                if(await_data_get.result[0] && await_data_get.result.length >=1){
                  currentArr.push(await_data_get.result[0]);//  .
                  MapProductEls.updateExclusive({ exclusive : currentArr });
                }else{
                  console.log('====>>>  moredata ==================');
                }
              }
            }
            loaderRef.current.style.display='none';
          }
        }
        else if(activeIndex == 1){
          // active    .
          //  --------------
          if(mapRightRedux.isProbroker.is){
            const currentArr = JSON.parse(JSON.stringify(productRedux.probroker));
            //    currentarr exclusive .          index.
            console.log('mapRight active,  onscroll ::');
            console.log(' currentArr:',currentArr,currentArr.length);//   mapData ??mapdata (idle ,   ,  searchdata origin .)   , 1. (,,) 2.     3.searchdata or mapdata     .    ,currentArr()    .
            
            loaderRef.current.style.display='flex';
            var scrollevent_server_async_request= async function(){
              
              let searchdetail_origindata = JSON.parse(localStorage.getItem("searchdetail_origin"));
              let mapData = JSON.parse(localStorage.getItem("mapData"));
              console.log('scrollevent    >>',searchdetail_origindata,mapData);//   searchdetail .
    
              if(mapData){
                var send_info = {
                  next_index : currentArr.length,
                  level : mapData.level, //mapdata   
                  lat : mapData.lat,
                  lng : mapData.lng,
                  screen_width : window.innerWidth,//
                  screen_height : window.innerHeight,
                }
              }else{
                console.log('====>>mapData  !!!!()=-========');
                return;
              }
              
              let probroker_data_more = await serverController.connectFetchController("/api/matterial/nextProbroker_item_sidebar",'POST',JSON.stringify(send_info));
              console.log('===>>probroker_data_more result::',probroker_data_more);
    
              return probroker_data_more;
            };
            var await_data_get = await scrollevent_server_async_request();
            console.log('await data gets::>>',await_data_get);
            
            if(await_data_get){
              if(await_data_get.result){
                if(await_data_get.result[0] && await_data_get.result.length >=1){
                  currentArr.push(await_data_get.result[0]);//  .
                  MapProductEls.updateProbroker({ probroker : currentArr });
                }else{
                  console.log('====>>>  moredata ==================');
                }
              }
            }
            loaderRef.current.style.display='none';
            /*currentArr.push({
              broker_id : 1,
              path:"/",
              tag2:"",
              tag3:"",
              tag4:"",
              name:"  New",
              address:"  104-5",
              sell_kind1:2,
              sell_kind2:7,
              sell_kind3:9,
            })
            MapProductEls.updateProbroker({ probroker : currentArr });*/
          }
        }
        else if(activeIndex == 2){
          //  
          //  active  .
          if(mapRightRedux.isBlock.is){
            const currentArr = JSON.parse(JSON.stringify(productRedux.block));
            //    currentarr exclusive .          index.
            console.log('mapRight active,  onscroll ::');
            console.log(' currentArr:',currentArr,currentArr.length);//   mapData ??mapdata (idle ,   ,  searchdata origin .)   , 1. (,,) 2.     3.searchdata or mapdata     .    ,currentArr()    .
            
            //console.log('====>>loader',loaderRef);
            loaderRef.current.style.display='flex';
            var scrollevent_server_async_request= async function(){
              
              let searchdetail_origindata = JSON.parse(localStorage.getItem("searchdetail_origin"));
              let mapData = JSON.parse(localStorage.getItem("mapData"));
              console.log('scrollevent    >>',searchdetail_origindata,mapData);//   searchdetail .
    
              if(mapData){
                var send_info = {
                  next_index : currentArr.length,
                  level : mapData.level, //mapdata   
                  lat : mapData.lat,
                  lng : mapData.lng,
                  screen_width : window.innerWidth,//
                  screen_height : window.innerHeight,
                }
              }else{
                console.log('====>>mapData  !!!!()=-========');
                return;
              }
              
              let complex_data_more = await serverController.connectFetchController("/api/matterial/nextComplex_item_sidebar",'POST',JSON.stringify(send_info));
              console.log('===>>complex_data_more result::',complex_data_more);
    
              return complex_data_more;
            };
            var await_data_get = await scrollevent_server_async_request();
            console.log('await data gets::>>',await_data_get);
            
            if(await_data_get){
              if(await_data_get.result){
                if(await_data_get.result[0] && await_data_get.result.length >=1){
                  currentArr.push(await_data_get.result[0]);//  .
                  MapProductEls.updateBlock({ block : currentArr });
                }else{
                  console.log('====>>>  moredata ==================');
                }
              }
            }
            loaderRef.current.style.display='none';
            /*currentArr.push({
              danji_id : 0,
              path:"/",
              title:` New`,
              address:"   200-13",
              date:"21.02.01",
              price:" 35,000",
              floor:"7",
            })
            MapProductEls.updateBlock({ block : currentArr });*/
          }
        }
      }
      //  
      else if(pageIndex == 2){
        const currentArr = JSON.parse(JSON.stringify(productRedux.brokerProduct));
        currentArr.push({
          isExc:true,
          item_id : 1,
          path:"/",
          startDate:"20.00.00",
          endDate: "20.00.00",
          kind:"",
          detail:" 999",
          type:"",
          price:"99 9,999",
          floor:"",
          Area:"",
          expenses:"",
          desc:" . "
        })
        MapProductEls.updateBrokerProduct({ brokerProduct : currentArr });
      }
      console.log("end");
    }
  }

  //    Event
  useEffect(() => {
    updatePageIndex(0);
  }, [mapRightRedux])
  
    return (
      <Container pageIndex={pageIndex} position={position} overflow={overflow} top={top} ref={containerRef} className="sideBarWrap" onScroll={()=>onScrollList()}>
        {
          pageLoader(updateReserveModal)
        }
        {/*
          productRedux.clickmarker.isclick? 
          <ClickMarkerClustSidebar setReport={setReport} updown={updown} setUpDown={setUpDown} containerRef={containerRef} status={productRedux.clickmarker.click_type}></ClickMarkerClustSidebar>
          :
          null
          */
        }
        <ModalCommon modalOption={modalOption}/>
        <ScrollLazyLoad ref={loaderRef}>
           <Img src={ScrollLoader2}/>
        </ScrollLazyLoad>
      </Container>
  );
}

const Container = styled.div`
  box-shadow:  -10px 0px 5px rgba(0, 0, 0, 0.16);
  position:fixed;
  right:0;
  top:80px;
  width:400px;height:100vh;
  padding-bottom:120px;
  overflow-y:scroll;
  content:'';
  background:#fff;
  scrollbar-width: none;
  -ms-overflow-style: none;
  &::-webkit-scrollbar {display: none;}
  @media ${(props) => props.theme.mobile} {
    width:100%;
    position:${({position}) => position};
    overflow-y:${({overflow}) => overflow};
    top:${({top}) => top};
    /* top:calc(100vw*(64/428)); */
    padding-bottom:calc(100vw*(150/428));
    /* overflow-y:hidden; */
    ${({pageIndex})=>{
      return pageIndex != 0 ?
      `
      top:calc(100vw*(0/428));
      `
      :
      `  `
    }}
  }
`
const ScrollLazyLoad = styled.div`
  position:fixed; width:400px;height:100px; bottom:0;right:0; 
  display:flex;flex-flow:row wrap;justify-content:center;align-items:center; display:none;
`;
const Img = styled.img`
  display:block; width:80px;height:80px;
`;
